    "code": "import React from 'react';\n\nimport PropTypes from 'prop-types';\nimport * as Styles from './style';\nimport { Warning } from '../Icon';\nimport Text from '../Text';\n\nconst Input = ({\n  disabled,\n  hasError,\n  help,\n  label,\n  name,\n  onChange,\n  onBlur,\n  placeholder,\n  size,\n  type,\n  value,\n}) => (\n  <Styles.InputWrapper>\n    {label.length > 0 && <Text htmlFor={name} type='label'>{label}</Text>}\n    <Styles.InputStyled\n      disabled={disabled}\n      hasError={hasError}\n      name={name}\n      onChange={onChange}\n      onBlur={onBlur}\n      placeholder={placeholder}\n      type={type}\n      size={size}\n      value={value}\n    />\n    {help.length > 0 && (\n      <Styles.HelpTextWrapper>\n        {hasError && <Warning size=\"medium\" />}\n        <Styles.HelpText type='help' htmlFor={name} hasError={hasError}>\n          {help}\n        </Styles.HelpText>\n      </Styles.HelpTextWrapper>\n    )}\n  </Styles.InputWrapper>\n);\n\nInput.propTypes = {\n  /** It disables the input field. </i> */\n  disabled: PropTypes.bool,\n  /** It colors the field in red. */\n  hasError: PropTypes.bool,\n  /** It adds an help text below the input box. */\n  help: PropTypes.string,\n  /** It adds a label on top of the input box. */\n  label: PropTypes.string,\n  /** It's the name of the input. */\n  name: PropTypes.string.isRequired,\n  /** It's the placeholder value of the input. */\n  placeholder: PropTypes.string,\n  /** The onChange event */\n  onChange: PropTypes.func.isRequired,\n  /** The onBlur event */\n  onBlur: PropTypes.func,\n  /** This is the vertical size of the input field, can be `small`, `regular`, or `tall` */\n  size: PropTypes.string,\n  /** The type of the input */\n  type: PropTypes.string,\n  /** The value of the input */\n  value: PropTypes.string,\n};\n\nInput.defaultProps = {\n  disabled: false,\n  hasError: false,\n  help: '',\n  label: '',\n  placeholder: '',\n  size: 'regular',\n  type: 'text',\n  value: undefined,\n  onBlur: () => {},\n}\n\nexport default Input;\n",
