      "code": "import React from 'react';\nimport Modal from '@bufferapp/ui/Modal';\nimport Text from '@bufferapp/ui/Text';\n\n\n/** Wide Modal */\nexport default function ModalTest() {\n  return (\n    <div style={{ width: '100%', height: '400px', position: 'relative' }}>\n      <Modal\n        action={{ label: \"Dont Panic!\" }}\n        wide\n      >\n        <div>\n          <div style={{ width: '100%', padding: '16px', boxSizing: 'border-box' }}>\n            <Text type=\"h3\">Space, it says, is big. Really big. You just wont believe how vastly, hugely, mindbogglingly big it is. I mean, you may think its a long way down the road to the chemists, but thats just peanuts to space.</Text>\n          </div>\n        </div>\n      </Modal>\n    </div>\n  );\n}\n",
