      "code": "import React from 'react';\nimport Select from '@bufferapp/ui/Select';\n\n/** Basic */\nexport default function ExampleSelect() {\n  return (\n    <Select\n      onSelectClick={() => console.info('Main select clicked')}\n      label=\"Click Me\"\n      keyMap={{\n        id: '_id',\n        title: 'name',\n      }}\n      items={[\n        {\n          _id: '1',\n          name: 'OpenOpenOpenOpenOpenOpenOpenOpenOpen',\n          hotKeyPrompt: 'Q',\n          disabled: true,\n        },\n        { _id: '2', name: 'Pending' },\n        { _id: '3', name: 'Closed' },\n        { _id: '4', name: 'Open' },\n        { _id: '5', name: 'Pending' },\n        { _id: '6', name: 'Closed' },\n        { _id: '7', name: 'Open' },\n        { _id: '8', name: 'Pending' },\n        { _id: '9', name: 'Closed' },\n        { _id: '10', name: 'Open' },\n        { _id: '11', name: 'Pending' },\n        { _id: '23', name: 'Closed' },\n        { _id: '21', name: 'Open' },\n        { _id: '22', name: 'Pending' },\n        { _id: '33', name: 'Closed' },\n        { _id: '41', name: 'Open' },\n        { _id: '52', name: 'Pending' },\n        { _id: '63', name: 'Closed' },\n      ]}\n      hotKeys={[\n        {\n          hotKey: 81,\n          onKeyPress: () => {\n            console.info('hey');\n          },\n        },\n        {\n          hotKey: 87,\n          onKeyPress: () => {\n            console.info('hello there');\n          },\n        },\n      ]}\n      hasCustomAction\n      onCustomItemClick={string => console.info(string)}\n      customItemLabel=\"Create Tag\"\n    />\n  );\n}\n",
